function Navbar() {
  return (
    <header className="site-navbar">
      <div className="nav-brand">
        <NavLink to="/">Khoa hc Tm linh</NavLink>
      </div>
      <nav className="nav-links">
        <NavLink to="/">Trang ch</NavLink>
        <NavLink to="/dich-vu">Dch v</NavLink>
        <NavLink to="/dich-vu/tarot-free-1-card">Tarot 1 l</NavLink>
        <NavLink to="/cua-hang">Ca hng</NavLink>
        <NavLink to="/kien-thuc">Kin thc</NavLink>
        <NavLink to="/lien-he">Lin h</NavLink>
      </nav>
    </header>
  );
}

export default Navbar;
